    SYNTAX:
        - components: Component ( Title Case )  -- SC ( simple component )  -- CT( Container ) --FIX( Fix )
 */
.Loading--SC .overlay {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.54);
}
.Loading--SC .contain-dots {
  z-index: 2;
  width: 150px;
  height: 110px;
  margin-top: 30px;
}
.Loading--SC .contain-dots .message {
  width: 100%;
  text-align: center;
  top: -5px;
  color: rgba(255, 255, 255, 0.54);
  font-weight: 500;
  font-size: 11px;
  letter-spacing: 2px;
}
.Loading--SC .contain-dots .message:before {
  height: 2px;
  width: 100%;
  left: 0;
  bottom: -5px;
  content: '';
  border-bottom: 1px solid rgba(255, 255, 255, 0.54);
}
.Loading--SC .contain-dots .message:after {
  height: 2px;
  width: 10%;
  left: 0;
  bottom: -5px;
  content: '';
  border-bottom: 2px solid #ff5722;
  animation: loadingText 1200ms infinite ease-in-out;
}
.Loading--SC .contain-dots svg {
  position: absolute;
  width: 150px;
  height: 110px;
}
.Loading--SC .contain-dots svg ellipse {
  cx: 25px;
  cy: 10px;
  rx: 5px;
  ry: 5px;
  stroke-width: 2px;
  transform-origin: center;
}
.Loading--SC .contain-dots svg:nth-of-type(1) ellipse {
  stroke: #ffab91;
  fill: none;
  cx: 25px;
  cy: 54px;
  animation: jump 500ms infinite ease-in-out;
  animation-delay: 0ms;
}
.Loading--SC .contain-dots svg:nth-of-type(2) ellipse {
  stroke: #ff8a65;
  fill: none;
  cx: 50px;
  cy: 54px;
  animation: jump 500ms infinite ease-in-out;
  animation-delay: 70ms;
}
.Loading--SC .contain-dots svg:nth-of-type(3) ellipse {
  stroke: #ff7043;
  fill: none;
  cx: 75px;
  cy: 54px;
  animation: jump 500ms infinite ease-in-out;
  animation-delay: 140ms;
}
.Loading--SC .contain-dots svg:nth-of-type(4) ellipse {
  stroke: #ff5722;
  fill: none;
  cx: 100px;
  cy: 54px;
  animation: jump 500ms infinite ease-in-out;
  animation-delay: 210ms;
}
.Loading--SC .contain-dots svg:nth-of-type(5) ellipse {
  stroke: #f4511e;
  fill: none;
  cx: 125px;
  cy: 54px;
  animation: jump 500ms infinite ease-in-out;
  animation-delay: 280ms;
}
.Loading--SC .contain-dots svg:nth-of-type(6) ellipse {
  fill: #000000;
  rx: 7px;
  ry: 3px;
  cy: 60px;
  cx: 25px;
  opacity: 0.10;
  animation: shadow 500ms infinite ease-in-out;
  animation-delay: 0ms;
}
.Loading--SC .contain-dots svg:nth-of-type(7) ellipse {
  fill: #000000;
  rx: 7px;
  ry: 3px;
  cy: 60px;
  cx: 50px;
  opacity: 0.10;
  animation: shadow 500ms infinite ease-in-out;
  animation-delay: 70ms;
}
.Loading--SC .contain-dots svg:nth-of-type(8) ellipse {
  fill: #000000;
  rx: 7px;
  ry: 3px;
  cy: 60px;
  cx: 75px;
  opacity: 0.10;
  animation: shadow 500ms infinite ease-in-out;
  animation-delay: 140ms;
}
.Loading--SC .contain-dots svg:nth-of-type(9) ellipse {
  fill: #000000;
  rx: 7px;
  ry: 3px;
  cy: 60px;
  cx: 100px;
  opacity: 0.10;
  animation: shadow 500ms infinite ease-in-out;
  animation-delay: 210ms;
}
.Loading--SC .contain-dots svg:nth-of-type(10) ellipse {
  fill: #000000;
  rx: 7px;
  ry: 3px;
  cy: 60px;
  cx: 125px;
  opacity: 0.10;
  animation: shadow 500ms infinite ease-in-out;
  animation-delay: 280ms;
}
@keyframes shadow {
  45% {
    opacity: .05;
    rx: 0px;
    ry: 0px;
    transform: translateY(5px) scale(0.7);
  }
}
@keyframes loadingText {
  50% {
    width: 50%;
    right: 0;
    left: auto;
    opacity: 0.7;
  }
}
@keyframes jump {
  60% {
    transform: translateY(-20px) scale(0.7);
    opacity: .9;
  }
  25% {
    rx: 5px;
    ry: 5px;
  }
  15% {
    rx: 7px;
    ry: 2px;
  }
}
